.game-box {
	display: flex;
	flex-direction: column;
	width: 100%;
	height: 795px;
	background: white;
	border: 1px solid var(--blue);
	border-radius: 4px;
	position: relative;
	border-radius: 5px;
	box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, 0.75);
}
.game-box .tab-menu {
	display: flex;
	justify-content: flex-start;
	width: 95%;
	overflow-x: auto;
}

.game-box .tab-menu .tab {
	font-size: 0.9rem;
	padding: 1rem 1rem;
	background: var(--sage);
	border-right: 1px solid var(--sky);
	cursor: pointer;
	color: white;
}

.game-box .tab-menu .btn-div {
	background: var(--sage);
	color: white;
}
.game-box .tab-menu .tab:hover {
	background: var(--blue);
}
.game-box .tab-menu button {
	border: none;
	outline: none;
}
.game-box .game-area {
	width: 100%;
	height: 100%;
	background: var(--gray);
	border-top: none;
}
.game-box .tab-menu .tab.active {
	border-bottom: none;
	background: var(--gray);
	color: var(--black);
	font-weight: 600;
}

.game-box iframe {
	border: none;
	width: 100%;
	height: 100%;
}

@media only screen and (max-width: 1200px) {
	.game-box .tab-menu .tab {
		font-size: 0.9rem;
		padding: 1rem 1rem;
	}
}
