.header {
	max-width: 100vw;
	width: 100%;
	height: fit-content;
	background: var(--gray);
	padding: 1rem 1rem 0.5rem 1rem;
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 0.3rem;
}
.header .first-row {
	width: 100%;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	gap: 1rem;
	flex: 1;
}
.header .first-row .title {
	flex: 3;
}
.header .second-row {
	width: 100%;
	display: flex;
	justify-content: flex-end;
}
.header .name {
	font-weight: 800;
	font-size: 2rem;
	color: var(--blue);
}
.header .email {
	font-weight: 800;
	font-size: 1.2rem;
	color: var(--sage);
}

.header .social {
	display: flex;
	justify-content: flex-end;
	gap: 0.5rem;
}
.header .social a {
	width: 30px;
	height: 30px;
}

@media only screen and (max-width: 550px) {
	.header {
		padding: 1rem 0.5rem 0.5rem 1rem;
		gap: 0.2rem;
	}
	.header .first-row {
		gap: 0.5rem;
	}
	.header .first-row .social {
		display: none;
	}
	.header .title .name {
		font-size: 1.5rem;
	}
	.header .title .email {
		font-size: 1rem;
	}
	.header .social {
		gap: 0.2rem;
	}
	.header .social a {
		width: 25px !important;
		height: 25px !important;
	}
}
