.today {
	font-size: 0.8rem;
	display: flex;
	flex-direction: column;
	gap: 1rem;
}

.quote-box .quote {
	padding: 0 1rem;
}
.quote-box .quote .text {
	font-style: italic;
}
.quote-box .quote .author {
	padding: 0.3rem 0;
	text-align: right;
}
.right-side {
	background: var(--sage);
	padding: 1rem;
}
.right-side .box {
	border-radius: 4px;
	display: flex;
	flex-direction: column;
	padding: 0.7rem;
	background: var(--ocean);
	box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, 0.75);
}

.right-side .box .heading {
	font-size: 1rem;
	font-weight: bold;
	padding: 0.5rem 0 1rem 0;
	color: var(--black);
}
@media only screen and (max-width: 550px) {
	.right-side .box {
		margin-bottom: 0;
		padding: 0.4rem;
	}
	.right-side .box .heading {
		font-size: 0.9rem;
	}
	.right-side .big-text {
		font-size: 1rem;
	}
}
