.plant {
	background: white;
	position: relative;
}
.plant .search-field {
	padding: 1rem;
	outline: none;
	border: none;
	background: var(--gray);
	color: black;
	font-size: 1rem;
}
.plant .search-container {
	padding: 1rem 0;
}
.plant .search-btn,
.plant .clear-btn {
	padding: 1rem;
	border-radius: 0px;
	font-size: 1rem;
}
.plant .search-results {
	display: flex;
	flex-wrap: wrap;
	gap: 1rem;
}
.plant-preview {
	margin-bottom: 0.5rem;
	display: grid;
	grid-template-columns: auto auto;
	grid-template-areas:
		"plant-name plant-name"
		"cycle      plant-img"
		"sun        plant-img"
		"water      plant-img";
	width: 330px;
	font-size: 0.9rem;
	row-gap: 0.4rem;
	position: relative;
}
.plant-preview .heart-icon {
	position: absolute;
	top: 0.5rem;
	right: 0.5rem;
	font-size: 1.2rem;
	opacity: 0.2;
	cursor: pointer;
}
.plant-preview .heart-icon.saved {
	color: red;
	opacity: 1;
}
.plant-preview .subtitle {
	padding: 0.5rem 0;
	font-size: 0.8rem;
	color: var(--blue);
}
.plant-preview .icon {
	color: var(--sage);
}
.plant-preview .plant-name {
	text-transform: capitalize;
	grid-area: plant-name;
	font-size: 1.1rem;
	font-weight: bold;
	display: flex;
	flex-direction: column;
}
.plant-preview .cycle {
	margin-top: 1rem;
	grid-area: cycle;
}
.plant-preview .sun {
	grid-area: sun;
}
.plant-preview .water {
	grid-area: water;
}
.plant-preview .plant-img {
	grid-area: plant-img;
	display: flex;
	justify-content: flex-end;
}
.plant-preview img {
	width: 100px;
	object-fit: contain;
	border-radius: 10%;
}
