:root {
	--dark: #29353c;
	--blue: #44576d;
	--sage: #668598;
	--ocean: #aac7d8;
	--sky: #dfebf6;
	--gray: #e6e6e6;
	--black: #333;
	--light-gray: #eee;
	--dark-gray: #888;
	--accent: rgb(247, 117, 57);
}
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: "Comfortaa", cursive;
}
html,
body {
	height: 100%;
	min-height: 100%;
}
body {
	background: var(--gray);
	background: linear-gradient(90deg, var(--dark) 34%, var(--sage) 97%);
}
.border {
	padding: 1rem;
	border: 1px solid var(--gray);
	border-radius: 4px;
	box-shadow: 4px 3px 5px 0px rgba(100, 100, 100, 0.75);
	-webkit-box-shadow: 4px 3px 5px 0px rgba(100, 100, 100, 0.75);
	-moz-box-shadow: 4px 3px 5px 0px rgba(115, 103, 103, 0.75);
}

.title {
	font-size: 1.2rem;
	font-weight: bold;
	color: var(--black);
	vertical-align: middle;
}
.standout {
	color: #914022;
	font-weight: 800;
	display: inline;
}
.footer {
	display: none;
}
.main-container {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: stretch;
	max-width: 100vw;
	width: 100%;
	height: 100%;
	position: relative;
}
.main-container .right-side {
	flex: 1;
	min-width: 280px;
	width: 100%;
	height: fit-content;
}
.main-container .left-side {
	flex: 3;
	padding: 1rem;
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
}
.main-container .left-side .box {
	margin-bottom: 1rem;
	padding: 1rem;
	border-radius: 5px;
	box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, 0.75);
}
.main-container .left-side .indent {
	padding-left: 0.8rem;
}

.btn {
	padding: 0.5rem 1rem;
	border-radius: 5px;
	background: var(--gray);
	color: var(--blue);
	cursor: pointer;
	font-weight: 600;
	border: none;
}
.btn.small-btn {
	font-size: 0.6rem;
	padding: 0.4rem 0.4rem;
	margin-top: 0;
	white-space: nowrap;
}
.btn:hover {
	background: var(--blue);
	color: white;
	font-weight: 600;
}
.link {
	cursor: pointer;
	font-weight: 600;
	font-size: 0.8rem;
	text-decoration: none;
}

.btn-div {
	display: flex;
	justify-content: flex-end;
}
.error {
	color: red;
	padding: 1rem;
	font-weight: bold;
}

.msg {
	color: blue;
	padding: 1rem;
	font-weight: bold;
}
.big-text {
	font-size: 1.5rem;
}
.med-text {
	font-size: 1rem;
}
.small-text {
	font-size: 0.8rem;
}

.icon {
	padding-right: 0.5rem;
}

.spacer {
	padding: 0 1rem;
}
.close-btn {
	border: none;
	outline: none;
	background: inherit;
	font-size: 1.5rem;
	cursor: pointer;
	color: var(--black);
	font-weight: 600;
	justify-self: flex-end;
	position: absolute;
	top: 0;
	right: 0;
	padding: 0.5rem;
}
/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 550px) {
	.footer {
		display: block;
	}
}
/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (max-width: 800px) {
	.main-container {
		flex-direction: column-reverse;
	}
	.main-container .left-side {
		padding: 1rem 1rem;
		width: 100%;
		max-width: 100vw;
	}
	.main-container .right-side {
		width: 95vw;
	}
	.main-container .right-side .today {
		flex-direction: row;
		gap: 0.5rem;
	}
	.main-container .right-side .quote-box {
		flex: 1;
	}
	.main-container .right-side .quote-box .quote {
		padding: 0 0.5rem;
	}
	.main-container .right-side .weather {
		flex: 2;
	}
	.main-container .right-side .weather .current {
		justify-content: space-around;
	}
	.main-container .right-side .weather .weather-details {
		margin: 0 0.8rem;
	}
	.main-container .right-side .weather .btn-div {
		justify-content: center;
	}
	.main-container .right-side .cat-box {
		display: none;
	}

	.main-container .right-side .weather .grid {
		display: none;
	}
	.main-container .right-side .weather .credit {
		display: none;
	}
}
